
import { Options, Vue } from 'vue-class-component';

@Options({
  props: {
    maze: Array,
    goalX: Number,
    goalY: Number
  },
  emits: ['stop', 'start'],
  watch: {
    maze() {
      /* If the maze changes, reset character position and started status. */
      this.charX = 0;
      this.charY = 0;
      this.started = false;
    }
  }
})
export default class MazeView extends Vue {
  maze!: number[][];
  goalX!: number;
  goalY!: number;

  private charX = 0;
  private charY = 0;
  private started = false;

  get height() {
    return this.maze.length;
  }

  private checkWin() {
    if (this.charX === this.goalX && this.charY === this.goalY) {
      this.$emit('stop');
    }
  }

  mounted() {
    const arrowUp = () => {
      if (this.maze[this.charY][this.charX] & 2) {
        this.charY -= 1;
        this.checkWin();
      }
    };

    const arrowDown = () => {
      if (this.maze[this.charY][this.charX] & 1) {
        this.charY += 1;
        this.checkWin();
      }
    };

    const arrowLeft = () => {
      if (this.maze[this.charY][this.charX] & 8) {
        this.charX -= 1;
        this.checkWin();
      }
    };

    const arrowRight = () => {
      if (this.maze[this.charY][this.charX] & 4) {
        this.charX += 1;
        this.checkWin();
      }
    };

    const start = () => {
      if (!this.started) {
        this.started = true;
        this.$emit('start');
      }
    };

    /* Bind arrowkeys to character movement */
    window.addEventListener('keydown', function(e) {
      start();
      switch (e.key) {
        case 'ArrowUp':
          arrowUp();
          break;
        case 'ArrowDown':
          arrowDown();
          break;
        case 'ArrowLeft':
          arrowLeft();
          break;
        case 'ArrowRight':
          arrowRight();
          break;
        default:
          break;
      }
    });
  }
}
